<template>
    <div>
        <el-table
                :data="imageList"
                :fit="false">

            <el-table-column
                    label="水库"
                    prop="rname"
                    width="100"
                    align="center"
            ></el-table-column>
            <el-table-column
                label="缩略图"
                width="150"
                align="center"
                >
                <template slot-scope="scope">
                    <el-image
                        :src="scope.row.path.substring(6)"
                    :preview-src-list="[scope.row.path.substring(6)]"></el-image>
                </template>
            </el-table-column>
            <el-table-column
                    label="图片路径"
                    prop="path"
                    header-align="center"
            ></el-table-column>
            <el-table-column
                    label="日期"
                    prop="date"
                    width="150"
                    align="center"
            ></el-table-column>
            <el-table-column
                    label="数据来源"
                    prop="type"
                    width="100"
                    align="center"
            ></el-table-column>
            <el-table-column
                    label="下载"
                    align="center"
                    width="100"
            >
                <template slot-scope="scope">
                    <el-button
                            @click="download(scope.row.type,scope.row.id)">
                        <el-icon
                                class="el-icon-download"
                        ></el-icon>
                    </el-button>

                </template>
            </el-table-column>

        </el-table>
    </div>
</template>

<script>
    export default {
        name: "dataManagementImageTable",
        data: function () {
            return {
                imageList: [],
            }

        },
        methods: {
            download: function (type, id) {
                let elemIF = document.createElement('iframe');
                elemIF.src = '/api/download/image?type=' + type + '&id=' + id;
                elemIF.style.display = 'none';
                document.body.appendChild(elemIF)
            }
        },
        created() {
            this.axios.get('/api/data/image').then((response) => {
                if (response.status === 200) {
                    this.imageList = response.data;
                } else {
                    console.log('error');
                }
            })
        }
    }
</script>

